import React from 'react';
import './App.css';
import Header from "./Components/Header/Header";
import Navbar from "./Components/Nav/Navbar";
import {Dialogs} from "./Components/Nav/Links/Dialogs/Dialogs";
import {BrowserRouter, Route} from 'react-router-dom'
import Profile from "./Components/Profile/Profile";
import Message from "./Components/Nav/Links/Message/Message";
import News from "./Components/Nav/Links/News/News";
import Settings from "./Components/Nav/Links/Settings/Settings";
import Music from "./Components/Nav/Links/Music/Music";

function App() {
    return (
        <BrowserRouter>
            <div className={'app-wrapper'}>
                <Header/>
                <Navbar/>
                <div className={'app-profile'}>
                    <Route path={'/dialog'} component={Dialogs}/>
                    <Route path={'/profile'} component={Profile}/>
                    <Route path={'/message'} component={Message}/>
                    <Route path={'/news'} component={News}/>
                    <Route path={'/settings'} component={Settings}/>
                    <Route path={'/music'} component={Music}/>
                </div>
            </div>
        </BrowserRouter>
    );
}

export default App;
