import React from "react";
import s from  './MyPosts.module.css'
import Post from "./Post/Post";




function MyPosts() {

    let posts = [
        {id:1, message:'Hello how are u?', likesCount:15, img:'https://api.penit.ink/uploads/avatars/default.png'},
        {id:2, message:'I am fine, thank you', likesCount:8, img:'https://cdn4.iconfinder.com/data/icons/heroes-villains-vol-1-colored/100/Batman-256.png'},
        {id:3, message:'What about u ?', likesCount:4, img:'https://cdn2.iconfinder.com/data/icons/heroes-villains-vol-2-colored-1/100/Captain_America-256.png'},
    ]

    let postsElement = posts.map(p =>  <Post message={p.message} like={p.likesCount} img={p.img}/>)

    return <div className={s.myPosts}>
        <div>My posts</div>
        <textarea cols={80} rows={5}>a</textarea>
       <div><button>Send</button></div>
        {postsElement}
    </div>
}

export default MyPosts