import React from "react";
import s from './Dialog.module.css'
import {NavLink} from "react-router-dom";

type DialogsItemProps = {
    name: string
    id: number
}

type MessagePropsType = {
    message: string
}

function DialogItem(props: DialogsItemProps) {
    let path = '/dialog/' + props.id
    return <div className={s.dialog}><NavLink to={path} className={s.dialog} id={'/dialog/' + props.id}>{props.name}</NavLink></div>
}

function Message(props: MessagePropsType) {
    return <div className={s.message}>{props.message}</div>
}

export function Dialogs() {

    let dialogs = [
        {id: 1, name: 'Kostya'},
        {id: 2, name: 'Julia'},
        {id: 3, name: 'Helena'},
        {id: 4, name: 'Zoi'},
        {id: 5, name: 'Ahri'},
    ]

    let messages = [
        {id: 1, message: 'Hello, how are u?'},
        {id: 2, message: 'I am fine, what about u?'},
        {id: 3, message: 'Do u love ReactJs\''},
        {id: 4, message: 'Zoi'},
        {id: 5, message: 'Ahri'},
    ]

    let dialogsElement = dialogs.map( dialog => <DialogItem name={dialog.name} id={dialog.id}/> )
    let messagesElement = messages.map(message => <Message message={message.message}/>)

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {dialogsElement}
            </div>

            <div className={s.messages}>
                {messagesElement}
            </div>
        </div>
    )
}

export default Dialogs